}

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 185px;
  height: 96px;
  text-decoration: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__logo {
    z-index: 2;
  }
}
.main-nav__logo--active {
  cursor: auto;
}

.main-nav__logo-image {
  display: block;
  padding: 0;
  margin: 0 auto;
}

.main-nav__toggle {
  position: absolute;
  content: "";
  z-index: 3;
  top: 0;
  right: 0;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle {
  top: 17px;
  right: 17px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  content: "";
  top: 9px;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}
.main-nav--closed .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 18px;
  right: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 26px;
  right: 17px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--opened .main-nav__list {
    display: block;
  }

  .main-nav--nojs {
    position: static;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list {
    position: static;
    display: block;
  }
}
.main-nav__list {
  position: absolute;
  width: 320px;
  padding: 0;
  margin: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__list {
    width: 768px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    width: 1200px;
  }
}

.main-nav__item {
  text-align: center;
}
.main-nav__item:first-child {
  grid-column: 1/2;
}
.main-nav__item:nth-child(2) {
  grid-column: 2/3;
}
.main-nav__item:nth-child(3) {
  grid-column: 4/5;
}
.main-nav__item:last-child {
  grid-column: 5/6;
}
@media (max-width: 767px) {
  .main-nav__item {
    position: relative;
    background-color: #81b3d2;
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__item:first-child {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
  }
  .main-nav__item:last-child {
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__item:last-child::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    height: 7px;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    mix-blend-mode: normal;
  }
}

